@import url("https://fonts.googleapis.com/css?family=Lora:400,700|Montserrat:300");
@import url("https://cdn.jsdelivr.net/gh/devicons/devicon@v2.13.0/devicon.min.css");

body, p {
    font-family: 'Lora', serif;
    font-size: 25px;
    font-weight: lighter;
    text-align: center;
}

h1, h2, h3, h4, h5, h6 {
    font-family: 'Montserrat', sans-serif;
    font-weight: 300;
    text-transform: uppercase;
}


/*
<!-- Navbar !-- >
 */

#navbar-link {
    margin: 0.3%;
}

.top-nav {
    width: 100%;
}

.navbar-color {
    background-color: #152238;
}

.nav {
    flex: 1;
    justify-content: flex-end;
}


.navbar-img {
    width: 25%;
    border-radius: 35%;
}

.smaller-picture {
    width: 15%;
    border-radius: 35%;
}

.smaller-font {
    font-size: large;
}
.smallest-font {
    visibility: hidden;
}

/*
Title
 */
.avatar-img {
    width: 20%;
    height: 50%;
    margin-top: 5%;
    margin-bottom: 3%;
}

.title-name {
}

.title-large {
    font-size: 60px;
}

.title-small {
    font-size: 30px;
}


.divider-custom {
    width: 100%;
    height: 10%;
    display: flex;
    justify-content: center;
    align-items: center;
}
.divider-custom .divider-custom-line {
    width: 100%;
    height: 5%;
    max-width: 7rem;
    background-color: #2c3e50;
    border-radius: 1rem;
    border-color: #2c3e50;
}
.divider-custom .divider-custom-line:first-child {
    margin-right: 1rem;
}
.divider-custom .divider-custom-line:last-child {
    margin-left: 1rem;
}
.divider-custom .divider-custom-icon {
    color: #2c3e50;
    font-size: 2rem;
}
.divider-custom.divider-light .divider-custom-line {
    height: 5%;
    width: 100%;
    background-color: #fff;
}
.divider-custom.divider-light .divider-custom-icon {
    color: #fff;
}

.intro-text {

}

.small-text {
    font-size: 13px;
    margin-left: 25px;
}

/*projects*/
.project-description {
    font-size: 16px;
}

/*contacts*
 */
.btn-circle.btn-xl {
    width: 105%;
    height: 50%;
    padding: 10px 16px;
    border-radius: 100%;
    line-height: 1.33;
    text-align: center;
    border-color: black;
    background-color: royalblue;
}
